import React, { useEffect, useState } from 'react';
import { Table, Badge, Container, Row, Col, Card } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import memberRecord from '../../../models/memberDetails';
import Loader from '../../layout/Loader';
import moment from 'moment';

function Members(props) {
    const [isShowLoader, setisShowLoader] = useState(false)
    const [memberRecords, setMemberRecords] = useState([]);
    const [loanAccounts, setLoanAccounts] = useState([]);

    useEffect(() => {
        console.log(props);
        getMemberData();
    }, [])

    const getMemberData = async() => {
        try{
            setisShowLoader(true);
            const response =await Promise.all(memberRecord.MemberDetailModel.getMemberPageData(props.location.state))
            setisShowLoader(false);
            setMemberRecords(response[0].body.message[0]);
            setLoanAccounts(response[1].body.message);          
        } catch (error) {
            setisShowLoader(false);
    }

        // setisShowLoader(true);
        // memberRecord.MemberDetailModel.getMemberDetailsData(props.location.state).then(res => {
        //     setisShowLoader(false);
        //     if (res.statusCode == 200) {
        //         setMemberRecords(res[0].body.message[0]);
        //     } else {
        //         setMemberRecords([]);
        //     }
        // })
    }
    const loanStatus=(loan)=>{
        let actionStatus = "";
        if (loan.is_approved == 1 && loan.is_disbursed == 1) {
            actionStatus = "Disbursed";
        } else if (loan.is_approved == 1 && loan.is_disbursed == 0) {
            actionStatus = "Approved";
        } else if (loan.is_disbursed == -1) {
            actionStatus = "Disburse Rejected";
        } else if (loan.is_approved == -1) {
            actionStatus = "Approval Rejected";
        } else {
            actionStatus = "Pending";
        }
        return actionStatus;
    }
    return (
        <>
            <Loader show={isShowLoader} />
            <Container fluid>
            <Row>
                    <Col>
                    <Card border="success" header
                            key={0}
                            text={'dark'}
                            className="m-2"
                        >
                            <Card.Header className="bg-info text-center"><b>Loan Accounts</b></Card.Header>
                            {isShowLoader==true?<Card.Body><h5 className="text-center">Loading...</h5></Card.Body>:
                            <Card.Body>{ loanAccounts.length==0?<h5 className="text-center">Not running any loan!</h5>:
                                <Table className="shadow-lg bg-white rounded small" striped bordered hover responsive>
                                <thead>
                                    <tr>
                                        <th>Loan Account</th> 
                                        <th>Application date</th> 
                                        <th>Scheme id</th>
                                        <th>Village name</th>  
                                        <th>EMI day</th>
                                        <th>EMI week</th>  
                                        <th>Member address</th>
                                        <th>Loan amount</th>
                                        <th>Interest rate</th>
                                        <th>EMI payout</th>
                                        <th>EMI type</th>
                                        <th>Tenure</th>
                                        <th>Status</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {loanAccounts.map(res=><tr>
                                        <td><Link to={{
                    pathname: '/loanApprovalDetails',
                    state: res.id
                }}><u>{res.loan_account_no}</u></Link></td>
                                        <td>{res.application_date}</td>
                                        <td>{res.scheme_id}</td>
                                        <td>{res.village_name}</td>
                                        <td>{res.day}</td>
                                        <td>{res.week}</td>
                                        <td>{res.address}</td>
                                        <td>{res.loan_amount}</td>
                                        <td>{res.interest_rate}</td>
                                        <td>{res.EMI_payout}</td>
                                        <td>{res.EMI_type}</td>
                                        <td>{res.Tenure}</td>
                                        <td><Badge className={loanStatus(res) == "Approved" || loanStatus(res) == "Disbursed" ? "bg-success" : "bg-danger"}>{loanStatus(res)}</Badge>
                                            </td>
                                    </tr>)}
                                </tbody>
                                </Table>
                                }
                            </Card.Body>
                            }
                        </Card>
                    </Col>
                </Row>

                <Row>
                    <Col>
                        <Card border="success" header
                            key={0}
                            text={'dark'}
                            className="m-2"
                        >
                            <Card.Header className="bg-success text-center"><b>Member Details</b></Card.Header>
                            <Card.Body>

                                <Table className="shadow-lg bg-white rounded small" striped bordered hover responsive>
                                    <tbody>
                                        <tr><th>Enrollment Date</th><td>{moment(memberRecords.enrollment_date).format("DD-MM-YYYY hh:ss A")}</td> </tr>
                                        <tr><th>Member Name</th>  <td>{memberRecords.member_name}</td></tr>
                                        <tr><th>Member Id</th>  <td>{memberRecords.member_id}</td></tr>
                                        <tr> <th>Gender</th>  <td>{memberRecords.gender}</td></tr>
                                        <tr><th>DOB</th>   <td>{moment(memberRecords.date_of_birth).format("DD-MM-YYYY")} </td></tr>
                                        <tr><th>Age</th> <td>{memberRecords.age}</td></tr>
                                        <tr><th>Marital Status</th>  <td>{memberRecords.marital_status}</td></tr>
                                        <tr><th>Mobile Number</th> <td>{memberRecords.mobile_number}</td></tr>
                                        <tr><th>Email Id</th> <td>{memberRecords.email_id}</td></tr>
                                        <tr> <th>Member Group Id</th>  <td>{memberRecords.member_group_id}</td></tr>
                                        <tr> <th>Aadhar Number</th> <td>{memberRecords.aadhar_number}</td></tr>
                                        <tr> <th>Driving License Number</th>  <td>{memberRecords.driving_license_number}</td></tr>
                                        <tr> <th>Voter Id Number </th>  <td>{memberRecords.voter_id_number}</td></tr>
                                        <tr>  <th>Ration Card Number</th> <td>{memberRecords.ration_card_number}</td></tr>
                                        <tr> <th>PAN Number</th> <td>{memberRecords.pan_card_number}</td></tr>
                                        <tr> <th>Bank Account no.</th> <td>{memberRecords.bank_account}</td></tr>
                                        <tr> <th>IFSC Code</th> <td>{memberRecords.bank_ifsc_code}</td></tr>
                                        <tr> <th>Bank Account Holder</th> <td>{memberRecords.bank_account_holder}</td></tr>

                                    </tbody>
                                </Table>
                            </Card.Body>
                        </Card>

                    </Col>
                </Row>
            </Container>
        </>
    );
}

export default Members;